<!-- 服务包管理列表 -->
<template>
  <div class="list-common-box">
    <!-- 标题 -->
    <div class="title-box">
      <Breadcrumb :isLevel='2'></Breadcrumb>
      <div class="rightbox">
        <div class="item" @click="handleAdd()">
           <cc-svg-icon icon-class="add"></cc-svg-icon>
           <span>新增</span>
        </div>
      </div>
    </div>
    <!-- 主要数据 -->
    <div class="card-box">
      <!-- 搜索 -->
      <div class="search-bar">
        <!-- 左 -->
        <div class="search-left">
          <div class="search-item">
            <span class="search-label">服务包名称</span>
            <el-input v-model.trim="searchKey.packageName" placeholder="请输入服务包名称" clearable></el-input>
          </div>
          <div class="search-item">
            <span class="search-label">服务包类型</span>
            <el-select placeholder="请选择服务包类型" v-model="searchKey.packageType" clearable>
              <el-option v-for="item in packageTypeOption" :key="item.value" :label="item.label" :value="item.value">
              </el-option>
            </el-select>
          </div>
          <div class="search-item">
            <span class="search-label">服务包标签</span>
            <el-select placeholder="请选择服务包标签" v-model="searchKey.labelNameList" clearable>
              <el-option v-for="item in labelNameListOption" :key="item.labelId" :label="item.labelName" :value="item.labelName">
              </el-option>
            </el-select>
          </div>
        </div>
        <div class="search-right">
          <el-button type="primary" plain icon="el-icon-search" @click="querylist()">查询</el-button>
          <el-button type="primary" plain icon="el-icon-delete" @click="Refreshlist()">清空</el-button>
        </div>
      </div>
      <!-- 表格 -->
      <div class="table-box">
        <el-table :data="dataList" tooltip-effect="dark" stripe height="100%"> 
          <el-table-column label="序号" align="center" width="80">
            <template slot-scope="scope">
              {{scope.$index+(pageNum - 1) * pageSize + 1}}
            </template>
          </el-table-column>
          <el-table-column prop="packageName" label="服务包名称" align="center">
            <template slot-scope="scope">
              {{scope.row.packageName?scope.row.packageName:EMPTYTEXT}}
            </template>
          </el-table-column>
          <el-table-column prop="packageType" label="服务包类型" align="center">
            <template slot-scope="scope">
              {{scope.row.packageType===1?"付费":scope.row.packageType===2?"免费":EMPTYTEXT}}
            </template>
          </el-table-column>
          <el-table-column prop="packageStatus" label="状态" align="center">
            <template slot-scope="scope">
              {{scope.row.packageStatus?packageStatusOption[scope.row.packageStatus]:EMPTYTEXT}}
            </template>
          </el-table-column>
          <el-table-column prop="originalPrice" label="服务价格" align="right">
            <template slot-scope="scope">
              {{scope.row.originalPrice?''+Number(scope.row.originalPrice).toFixed(2):EMPTYTEXT}}
            </template>
          </el-table-column>
          <el-table-column prop="discountPrice" label="优惠价格" align="right">
            <div slot-scope="scope">
               {{scope.row.discountPrice?''+Number(scope.row.discountPrice).toFixed(2):EMPTYTEXT}}
            </div>
          </el-table-column>
          <el-table-column prop="registerUser" label="创建人" align="center">
            <div slot-scope="scope">
              {{scope.row.registerUser?scope.row.registerUser:EMPTYTEXT}}
            </div>
          </el-table-column>
          <el-table-column prop="labelNameList" label="服务包标签" align="center">
            <div slot-scope="scope">
              {{scope.row.labelNameList?scope.row.labelNameList:EMPTYTEXT}}
            </div>
          </el-table-column>
          <el-table-column prop="createDateStr" label="上传时间"  align="center" width="170">
            <template slot-scope="scope">                  
              {{scope.row.createDateStr?scope.row.createDateStr:EMPTYTEXT}}
            </template>
          </el-table-column>
          <el-table-column label="操作"  align="center" width="120">
            <template slot-scope="scope">
              <cc-svg-icon icon-class="edit" @click="handleAdd(scope.row.packageId)"></cc-svg-icon>
              <cc-svg-icon icon-class="view" @click="handleDetail(scope.row.packageId)"></cc-svg-icon>
            </template>
          </el-table-column>
        </el-table>
      </div>
      <!-- 分页 -->
      <div class="page-box">
         <Paginaction class='pagination' :goPage="pageNum" :totalNum="totalNum" @updateList="changePage" ref="paginationEvent"/>
      </div>
    </div>
  </div>
</template>

<script>
  export default {
    name: 'servicePackage-list',
    data() {
      return {
        searchKey:{
          packageName:'',//服务包名称
          packageType:'',//服务包类型
          labelNameList:'' //标签名称
        },
        packageTypeOption:[
          {
            value: 0,
            label: '全部'
          },
          {
            value: 1,
            label: '付费'
          },
          {
            value: 2,
            label: '免费'
          }
        ],
        packageStatusOption:{
          1:'已启用',
          2:'已禁用'
        },
        labelNameListOption:[], //标签下拉选项
        pageNum:1,//当前页
        pageSize:10,//每页条数
        totalNum: 0, //总条数
        dataList: [] //服务包管理列表
      }
    },
    created() {
      this.getLabelist() //获取标签列表
      this.getDatalist() //获取服务包管理列表
    },
    methods: {
      //查看详情
      handleDetail(id) {
        this.$router.push({
          path: '/system-manage/servicePackage-manage/detail',
          query:{id:id}
        })
      },
      //添加、编辑服务包
      handleAdd(id) {
        this.$router.push({
          path: '/system-manage/servicePackage-manage/add',
          query:{id:id}
        })
      },
      //查询
      querylist() {
        this.pageNum = 1
        this.getDatalist()
      },
      //重置
      Refreshlist() {
        this.pageNum = 1
        this.pageSize = 10
        Object.keys(this.searchKey).forEach(k => this.searchKey[k] = '')
        this.getDatalist()
      },
      //分页改变
      changePage(query){
        this.pageNum = query.pageNum
        this.pageSize = query.pageSize
        this.getDatalist()
      },
      //获取标签列表
      getLabelist() {
        this.$emit("viewLoad", true);
        this.$http.post(process.env.VUE_APP_API + '/family/label/getBy').then(res => {
          if (res.code===200) {
            this.labelNameListOption = res.data || [] //标签列表
          } else {
            this.$message.error(res.msg);
          }
          this.$emit("viewLoad", false);
        })
        .catch(error => {
          this.$emit("viewLoad", false);
          this.$message.error(error);
        });
      },
      //获取服务包管理列表
      getDatalist(query) {
        this.$emit("viewLoad", true);
        let params = {}
        params.pageNum  = this.pageNum
        params.pageSize = this.pageSize
        params = Object.assign({},params,this.searchKey)
        this.$http.post(process.env.VUE_APP_API + '/family/servicePackage/getDtoByPage', params).then(res => {
          if (res.code===200) {
            this.dataList = res.data.list || [] //服务包列表
            this.totalNum = res.data.total //总条数
            this.pageNum = res.data.pageNum //当前页
            this.pageSize = res.data.pageSize //每页条数
          } else {
            this.$message.error(res.msg);
          }
          this.$emit("viewLoad", false);
        })
        .catch(error => {
          this.$emit("viewLoad", false);
          this.$message.error(error);
        });
      }
    }
  }
</script>
<style lang='less' scoped>
</style>